type Props = TokenType & {
  closeModal: () => void;
};
export const TokenRow: FunctionComponent<Props> = ({
  mintAddress,
  tokenName,
  tokenSymbol,
  icon,
  closeModal
}) => {
  const dispatch = useDispatch(); // eslint-disable-next-line unicorn/no-null

  const inputRef = useRef<HTMLInputElement>(null);
  const [isOpen, setIsOpen] = useState(false);

  const handleChevronClick = () => {
    setIsOpen(!isOpen);
  };

  const handleAddClick = () => {
    const mint = new web3.PublicKey(mintAddress);
    dispatch(createTokenAccount(mint));
    dispatch(getOwnedTokenAccounts());
    closeModal();
  };

  const handleCopyClick = () => {
    const input = inputRef.current;

    if (input) {
      input.focus();
      input.setSelectionRange(0, input.value.length);
      document.execCommand('copy');
    }
  };

  return <Wrapper>
      <ChevronWrapper onClick={handleChevronClick} className={classNames({
      opened: isOpen
    })}>
        <ChevronIcon name="chevron" />
      </ChevronWrapper>
      <Main>
        <Content>
          <InfoWrapper onClick={handleChevronClick}>
            <TokenAvatar src={icon} size={44} />
            <Info>
              <Top>
                <div>{tokenSymbol}</div> <div />
              </Top>
              <Bottom>
                <div>{tokenName}</div> <div />
              </Bottom>
            </Info>
          </InfoWrapper>
          <Button primary small onClick={handleAddClick}>
            Add
          </Button>
        </Content>
        <Additional className={classNames({
        opened: isOpen
      })}>
          <Input ref={inputRef} title={`${tokenSymbol} Mint Address`} value={mintAddress} readOnly postfix={<CopyWrapper onClick={handleCopyClick}>
                <CopyIcon name="copy" />
              </CopyWrapper>} />
        </Additional>
      </Main>
    </Wrapper>;
};